.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(141, 141, 137, 0.107);

}

.modal {
  background-color: aliceblue;
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
  
}